.rg-main-button {
    margin: 0 auto 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-style: none;
    box-sizing: border-box;
    width: 161.45px;
    height: 44px;
    border-radius: 22px;
    cursor: pointer;
    background: #ffffff;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(16px / 21);
    color: #d41342;
    margin-left: 10px;
    & svg {
      margin-left: 8px;
      stroke: #d41342;
    }
    &:hover,
    &:focus,
    &:active {
      background: #ECEBEB;
      color: #d41342;
      & svg,
      & svg,
      & svg {
        stroke: #d41342;
      }
    }
  }

.about{
    background-color: #fff5f6;

    &__tittle{
        text-transform: uppercase;
        text-align: center;

        &--label{
            color: #ffa5ba;
            font-family: 'Titan One', cursive;
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 21px;
            padding-top: 125px;
        }
        &--h1 {
            color: #d41443;
            font-family:  'Titan One', cursive;
            font-style: normal;
            font-weight: 400;
            font-size: 34px;
            line-height: 39px;
            letter-spacing: 0.04em;
            padding: 15px 60px 26px 60px;
        }
    }
    &__cow-img{
        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
    }

    &__description {
      

        &--first {
            color: #000000;
            font-family: 'DM Sans', sans-serif;
            font-weight: 500;
            font-size: 12px;
            line-height: 19.44px;
            padding: 0 50px 47px 50px;
        }
        
        &--second {
            color: #907e82;
            font-family: 'DM Sans', sans-serif;
            font-weight: 500;
            font-size: 12px;
            line-height: 19.44px;
            padding: 0 50px 0 50px;
        }

        &--third {
            color: #907e82;
            font-family: 'DM Sans', sans-serif;
            font-weight: 500;
            font-size: 12px;
            line-height: 19.44px;
            padding: 20px 50px 17px 50px;
        }
    }

    &__btn {
        width: 176px;
        height: 44px;
        background-color: #ffffff;
        border-radius: 22px;
        border: none;
        padding-left: 37px;
        margin-left: 50px;

        color: #d41443;
        font-family: 'DM Sans', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        align-items: center;
        text-align: center;
        display: flex;
        cursor: pointer;
        
    }

    @media screen and (max-width: 767px)
    {
        .rg-main-button
        {
            margin-left: 50px;
        }
    }

    @media screen and (max-width: 767px){
        &__bg-img {
            background-image: url(/images/about/milk-mobile.png);
            background-repeat: no-repeat;
            background-position: center 92%;
            background-size: 100%;
        }
    }
    
    @media screen and (min-width: 768px){
        &__tittle--label {
            font-size: 26px;
            line-height: 30px;
            padding-top: 130px;
        }

        &__tittle--h1 {
            font-size: 48px;
            line-height: 55px;
            padding-bottom: 0;
        }
        &__cow-and-text {
            display: flex;
        }
        &__description {
            &--first {
                font-size: 14px;
                line-height: 24.64px;
                padding-top: 100px;
                padding-left: 0;
                padding-right: 36px;
                padding-bottom: 49px;
            }
            &--second {
                font-size: 14px;
                line-height: 24.64px;
                padding-left: 0px;
                padding-right: 36px;
            }
            &--third{
                font-size: 14px;
                line-height: 24.64px;
                padding-left: 0;
                padding-right: 36px;
                padding-bottom: 62px;
            }
        }
        .rg-main-button {
            margin-left: -10px;
        }
        
        &__cow-img {
            padding-top: 50px;
            padding-right: 17px;
            padding-left: 39px;
        }
    }
    
    @media screen and (min-width: 1280px){

        &__bg-img{
            background-image: url(/images/about/milk-desktop.png);
            background-repeat: no-repeat;
            background-position: center 120%;
            background-size: 100%;
        }
        .rg-main-button
        {
            margin-left: -10px;
        }

        &__tittle--label {
            font-size: 30px;
            line-height: 34px;
            padding-top: 125px;
        }

        &__tittle--h1 {
            font-size: 58px;
            line-height: 66px;
        }
        
        &__description {
            &--first {
                font-size: 16px;
                line-height: 28.16px;
                letter-spacing: 0.04em;
                padding-top: 106px;
                padding-right: 107px;
                padding-bottom: 35px;
            }
            &--second {
                font-size: 14px;
                letter-spacing: 0.04em;
                line-height: 28.16px;
                padding-right: 107px;
            }
            &--third{
                font-size: 14px;
                letter-spacing: 0.04em;
                line-height: 28.16px;
                padding-top: 25px;
                padding-right: 107px;
                padding-bottom: 45px;
            }
        }
        &__cow-img {
            padding-top: 55px;
            padding-right: 74px;
            padding-left: 112px;
        }
    }
}

